import React from 'react';
import { useSelector } from 'react-redux';
import { Card } from 'antd';
const UserProfile = () => {
    const {userName, userMatch } = useSelector(state => state.user);
    return (
        <>
        <h1>{userName}</h1>
        {userMatch.map(match => {
            <Card key ={match.leagueId}>
            {/* // cover = {
            //     // <img src={require(`../images/${match.tier}.PNG`)} alt=""/>
            // }>
            //     <Card.Meta

            //     /> */}
            </Card>
        })}
        </>
    );
};

export default UserProfile;